<template>
	<div>
		<div class="container">
			<div class="row">
				<div class="col s12 m8 offset-m2">
					<div class="login card-panel blue white-text center">
						<h3>Ingrese</h3>
						<form>
							<div class="input-field">
								<i class="material-icons prefix">email</i>
								<input type="text" id="email">
								<label class="white-text" for="email">Email</label>
							</div>
							<div class="input-field">
								<i class="material-icons prefix">lock</i>
								<input type="password" id="password">
								<label class="white-text" for="password">Clave</label>
							</div>
							<button v-on:click="login" class="btn btn-large grey lighten-4 black-text">Ingrese</button>
						</form>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
import db from './firebaseInit'

export default {
	name: 'login',
	data: function() {
		return {
			email: '',
			password: ''
		}
	},
	methods: {
		login: function(e) {
			firebase
			.auth()
			.signInWithEmailAndPassword(this.email, this.password)
			.then(
				user => {
					alert('Tienes sesion con el usuario ${user.email}');
					this.$router.push('/');
				},
				err => {
				alert(err.message)
				}
			);

			e.preventDefault();
		}
	}
};
</script>


