<template>
	<div>
		<div class="container">
			<div class="row">
				<div class="col s12 m8 offset-m2">
					<div class="login card-panel grey lighten-4 black-text center">
						<h3>Registrarse</h3>
						<form>
							<div class="input-field">
								<i class="material-icons prefix">email</i>
								<input type="text" id="email" v-model="email">
								<label for="email">Email</label>
							</div>
							<div class="input-field">
								<i class="material-icons prefix">lock</i>
								<input type="password" id="password" v-model="password">
								<label for="password">Clave</label>
							</div>
							<button v-on:click="register" class="btn btn-large grey lighten-4 black-text">Registro</button>
						</form>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
import db from './firebaseInit'
export default {
	name: 'register',
	data () {
		return {
			email: null,
			password: null
		}
	},
	methods: {
		register: function(e) {
			firebase
			.auth()
			.createUserInWithEmailAndPassword(this.email, this.password)
			.then(
				user => {
					alert('Se creo el usuario ${user.email}');
					this.$router.push('/');
				},
				err => {
				alert(err.message)
				}
			);

			e.preventDefault();
		}
	}
}
</script>


